*{margin: 0; padding: 0;}
img{display: block;}
li{list-style: none;}

.header{position: fixed; height: 60px; left: 0; top: 0; width: 100%; z-index: 999; background-color: #FFF; border-bottom: 1px solid #EEE;}
.header .topLogo{position: absolute; left: 5px; top:15px}
.header .topLogo img{height: 30px;}
.header .gnbBtn{position: absolute; right: 10px; top: 10px;}

.gnb{position:fixed; top: 60px; right: -200px; width: 200px; height: calc(100vh - 60px); background-color: #000; z-index: 999; overflow: hidden; transition-duration: 0.5s;}
.gnb a{color: #FFF; display: block; text-align: center; line-height: 40px; text-decoration: none;}

.topBanner{position: relative; margin-top: 60px;}
.topBanner img{width: 100%;}
.topBanner h2{position: absolute; left: 50%; top: 50%; transform:translate(-50%,-50%) ; color: #FFF; text-shadow: 1px 1px 4px rgba(0,0,0,0.5);}

.topBanner .topList li{position: absolute; left: 0; top: 0;  width: 100%;}
.topBanner .topList li img{width: 100%;}

.prev{position: absolute; left: 20px; top: 50%; transform: translateY(-50%);}
.next{position: absolute; right: 20px; top: 50%; transform: translateY(-50%);}



.NewProduct{position: relative; width: 90%; margin: 100px auto; overflow: hidden;}
.NewProduct h2{font-size: 20px; margin-bottom: 50px; text-align: center;}
.NewWrap{position: relative;}
.NewWrap ul{position: relative; width: 266.64%; overflow: hidden;}
.NewWrap ul::after{content: ""; display: block; overflow: hidden;}
.NewWrap ul li{width: 11%; margin: 0 0.5%; float: left;}
.NewWrap ul li img{width: 90%; display: block; margin: 0 auto;}
.NewWrap .newPrev{position: absolute; left: 10px; top: 50%; transform: translateY(-50%);}
.NewWrap .newNext{position: absolute; right: 10px; top: 50%; transform: translateY(-50%);}




@media screen and (min-width:601px) {
  .best h2{font-size: 20px; line-height: 80px ; text-align: center;}
  .best ul{display: flex; justify-content: space-between; width: 94%; margin: 0 auto; flex-wrap: wrap;}
  .best ul li{width: 24%; text-align: center;}
  .best ul li img{width: 100%;}
  .best ul li h3{font-size: 13px; line-height: 2em;}
  .best ul li p{font-size: 12px; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;}
}
@media screen and (max-width:600px) {
  .best h2{font-size: 20px; margin-bottom: 50px; text-align: center;}
  .best ul{ width: 94%; margin: 0 auto;}
  .best ul li{text-align: center; display: flex; justify-content: space-between; margin-bottom: 10px;}
  .best ul li img{width: 45%;}
  .best ul li div{width: 50%; display: flex; flex-direction: column; justify-content: center;}
  .best ul li h3{font-size: 14px; line-height: 2em;}
  .best ul li p{font-size: 13px;}
}


.lightBox{position: fixed; left: 0; top: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.8);}
.lightInner{position: absolute; width: 70%; padding: 20px; background-color: #FFF; border-radius: 20px; left: 50%; top: 50%; transform: translate(-50%,-50%);  display: flex;}
.lightBox img{width: 50%;}
.lightBox .lightText{width: 50%; text-align: center; display: flex; flex-direction: column; justify-content: center;}
.lightBox .lightText h2{font-size: 18px;}
.lightBox .close{position: absolute; right: 20px; bottom: 20px; background-color: #000; color: #FFF; width: 60px; height: 26px;}

.accordion{position: relative; background-color: #EEE; margin: 100px 0; padding: 50px 0;}
.accordion h2{text-align: center; margin-bottom: 50px;}
.accordion ul{width: 90%; margin: 0 auto;}
.accordion li{padding-bottom: 5px;}
.accordion li h3{padding: 10px; background-color: #000; border-radius: 10px; color: #FFF;  font-size: 14px;}
.accordion li div{padding:3px 10px; height: 60px; font-size: 14px; overflow: hidden; transition-duration: 0.5s;}

.tabs{position: relative; width: 90%; margin: 100px auto;}
.tabs h2{text-align: center; margin-bottom: 50px;}
.tabs .tabsTitle{display: flex;}
.tabs .tabsTitle li{width: 50%; text-align: center; background-color: #EEE; line-height: 50px; margin-bottom:20px;}
.tabs .tabsTitle li.select{background-color: #222; color: #FFF;}
.tabs .tabsCon div{display: none;}
.tabs .tabsCon div.select{display: block;}
.tabs .tabsCon img{width: 100%;}

.visited{position: relative; width: 90%; margin: 100px auto;}
.visited h2{text-align: center; margin-bottom: 50px;}
.visited .inputBox{width: 100%; display: flex; display: flex; align-items: center; margin-bottom: 10px;}
.visited .inputBox input{width: calc(100% - 60px); height: 28px;}
.visited .inputBox button{width: 60px; height: 32px; background-color: #000; color: #FFF; border: none}
.visited li{padding: 5px 10px ; border: 1px solid #EEE; margin-bottom: 2px ; display: flex; border-radius: 5px; justify-content: space-between; position: relative; align-items: center; font-size: 13px;}
.visited li .close{display: block; width: 15px;height: 15px; text-align: center; line-height: 15px;color: #FFF; border-radius: 8px; background-color: #000; font-size: 12px;}